.console-box-list {
  padding: 10px 20px;
  border: 0;
}
.right-content-box {
  padding: 20px;
  box-sizing: border-box;
}
.right-content-box .equipment-lis {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #d4e1e9;
}
.right-content-box .equipment-lis .equipment-lis-left .img-box {
  width: 94px;
  height: 94px;
  margin-right: 25px;
  border: 1px solid #f0f4f7;
  background-image: url(../images/equipment.png);
  background-size: cover ;
  background-position: center center;
}
.right-content-box .equipment-lis .equipment-lis-left .equipment-info .name {
  color: #283e53;
  font-size: 18px;
}
.right-content-box .equipment-lis .equipment-lis-left .equipment-info .name:hover {
  color: #03acef;
}
.right-content-box .equipment-lis .equipment-lis-left .equipment-info .describe {
  margin: 5px 0;
  color: #6c849c;
}
.right-content-box .equipment-lis .equipment-lis-left .equipment-info .info-list {
  max-width: 560px;
}
.right-content-box .equipment-lis .equipment-lis-left .equipment-info .info-list li {
  min-width: 160px;
  margin-right: 20px;
}
.right-content-box .equipment-lis .equipment-lis-left .equipment-info .info-list li span {
  color: #9eabbc;
  font-size: 12px;
}
.right-content-box .equipment-lis .equipment-lis-right {
  line-height: 100px;
}
.right-content-box .equipment-lis .equipment-lis-right a {
  margin-right: 30px;
}
.modal#look .modal-dialog {
  width: 500px;
}
.modal#look .modal-body {
  padding: 15px 0;
}
.modal#look .modal-body .modal-body-content {
  width: auto;
}
.modal#look li {
  margin: 15px 0;
}
.modal#look li .lbs {
  color: #283e53;
  width: 85px;
  margin-right: 20px;
}
.modal#look li .lis-content {
  width: 275px;
}
.modal#look li .long-txt {
  display: block;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}
.modal#look li .long-txt-btn {
  color: #03acef;
}
.modal#look li .long-txt-btn .caret {
  border: 5px solid transparent;
  border-top: 5px dashed #03ACEF;
}
.modal#look li .long-txt-btn.active .caret {
  transform: rotate(180deg);
}
.modal#look li .img-box {
  position: relative;
  width: 94px;
  height: 94px;
}
.modal#look li .img-box .box-shadow-box {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
}
.modal#look li .img-box .box-shadow-box .iconfont {
  position: absolute;
  color: #fff;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.modal#look li .img-box:hover .box-shadow-box {
  display: block;
}
